"use client"

import { useState, useEffect } from "react"
import { NotificationSettings } from "@/types"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Switch } from "@/components/ui/switch"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Button } from "@/components/ui/button"

export default function WaterNotificationSettings() {
  const [settings, setSettings] = useState<NotificationSettings>({
    enabled: false,
    interval: 30
  })
  const [notificationSupported, setNotificationSupported] = useState(false)
  const [permission, setPermission] = useState<NotificationPermission>('default')
  const [intervalId, setIntervalId] = useState<NodeJS.Timeout | null>(null)
  const [message, setMessage] = useState<string>("")

  useEffect(() => {
    // Check if notifications are supported
    if ('Notification' in window) {
      setNotificationSupported(true)
      setPermission(Notification.permission)
    }

    // Load saved settings
    loadSettings()

    return () => {
      if (intervalId) {
        clearInterval(intervalId)
      }
    }
  }, [])

  const loadSettings = () => {
    try {
      const saved = localStorage.getItem('water-notification-settings')
      if (saved) {
        const parsed = JSON.parse(saved)
        setSettings(parsed)
        
        // If notifications were enabled, restart them
        if (parsed.enabled && Notification.permission === 'granted') {
          startNotifications(parsed.interval)
        }
      }
    } catch (error) {
      console.error('Error loading notification settings:', error)
    }
  }

  const saveSettings = (newSettings: NotificationSettings) => {
    try {
      localStorage.setItem('water-notification-settings', JSON.stringify(newSettings))
      setSettings(newSettings)
    } catch (error) {
      console.error('Error saving notification settings:', error)
    }
  }

  const requestPermission = async () => {
    if (!notificationSupported) {
      setMessage("Twoja przeglÄ…darka nie obsÅ‚uguje powiadomieÅ„")
      return false
    }

    try {
      const result = await Notification.requestPermission()
      setPermission(result)
      
      if (result === 'granted') {
        setMessage("Uprawnienia do powiadomieÅ„ zostaÅ‚y przyznane!")
        return true
      } else if (result === 'denied') {
        setMessage("Uprawnienia do powiadomieÅ„ zostaÅ‚y odrzucone. WÅ‚Ä…cz je w ustawieniach przeglÄ…darki.")
        return false
      } else {
        setMessage("ProÅ›ba o uprawnienia zostaÅ‚a anulowana")
        return false
      }
    } catch (error) {
      console.error('Error requesting notification permission:', error)
      setMessage("BÅ‚Ä…d podczas Å¼Ä…dania uprawnieÅ„ do powiadomieÅ„")
      return false
    }
  }

  const startNotifications = (intervalMinutes: number) => {
    if (intervalId) {
      clearInterval(intervalId)
    }

    const newIntervalId = setInterval(() => {
      if (Notification.permission === 'granted') {
        try {
          new Notification('Czas na wodÄ™! ðŸ’§', {
            body: 'PamiÄ™taj o nawodnieniu organizmu podczas treningu',
            icon: '/water-icon.png', // Optional: add a water icon to public folder
            tag: 'water-reminder',
            requireInteraction: false
          })
          
          // Update last notification time
          const updatedSettings = {
            ...settings,
            lastNotification: new Date()
          }
          saveSettings(updatedSettings)
        } catch (error) {
          console.error('Error showing notification:', error)
        }
      }
    }, intervalMinutes * 60 * 1000)

    setIntervalId(newIntervalId)
  }

  const stopNotifications = () => {
    if (intervalId) {
      clearInterval(intervalId)
      setIntervalId(null)
    }
  }

  const handleToggleNotifications = async (enabled: boolean) => {
    if (enabled) {
      if (permission !== 'granted') {
        const granted = await requestPermission()
        if (!granted) return
      }
      
      startNotifications(settings.interval)
      setMessage("Powiadomienia o piciu wody zostaÅ‚y wÅ‚Ä…czone!")
    } else {
      stopNotifications()
      setMessage("Powiadomienia o piciu wody zostaÅ‚y wyÅ‚Ä…czone")
    }

    const newSettings = { ...settings, enabled }
    saveSettings(newSettings)
  }

  const handleIntervalChange = (newInterval: number) => {
    const newSettings = { ...settings, interval: newInterval }
    saveSettings(newSettings)

    // Restart notifications with new interval if they're enabled
    if (settings.enabled && permission === 'granted') {
      startNotifications(newInterval)
    }
  }

  const testNotification = () => {
    if (permission === 'granted') {
      try {
        new Notification('Test powiadomienia ðŸ’§', {
          body: 'To jest testowe powiadomienie o piciu wody',
          tag: 'water-test'
        })
        setMessage("Testowe powiadomienie zostaÅ‚o wysÅ‚ane!")
      } catch (error) {
        console.error('Error showing test notification:', error)
        setMessage("BÅ‚Ä…d podczas wysyÅ‚ania testowego powiadomienia")
      }
    } else {
      setMessage("Najpierw przyznaj uprawnienia do powiadomieÅ„")
    }
  }

  return (
    <Card className="border-gray-200">
      <CardHeader>
        <CardTitle className="text-xl font-semibold text-black">
          Przypomnienia o piciu wody
        </CardTitle>
        <CardDescription className="text-gray-600">
          Konfiguruj automatyczne powiadomienia przypominajÄ…ce o nawodnieniu
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        {!notificationSupported && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4">
            <p className="text-red-700 text-sm">
              Twoja przeglÄ…darka nie obsÅ‚uguje powiadomieÅ„ push
            </p>
          </div>
        )}

        {notificationSupported && permission === 'denied' && (
          <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
            <p className="text-yellow-700 text-sm">
              Powiadomienia zostaÅ‚y zablokowane. WÅ‚Ä…cz je w ustawieniach przeglÄ…darki.
            </p>
          </div>
        )}

        <div className="flex items-center justify-between">
          <div className="space-y-1">
            <Label className="text-sm font-medium text-black">
              WÅ‚Ä…cz przypomnienia
            </Label>
            <p className="text-sm text-gray-600">
              Otrzymuj regularne powiadomienia o piciu wody
            </p>
          </div>
          <Switch
            checked={settings.enabled}
            onCheckedChange={handleToggleNotifications}
            disabled={!notificationSupported}
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="interval" className="text-sm font-medium text-black">
            InterwaÅ‚ powiadomieÅ„ (minuty)
          </Label>
          <Input
            id="interval"
            type="number"
            min="5"
            max="120"
            value={settings.interval}
            onChange={(e) => handleIntervalChange(Number(e.target.value))}
            className="border-gray-300"
            disabled={!notificationSupported}
          />
          <p className="text-xs text-gray-500">
            Minimalne 5 minut, maksymalne 120 minut
          </p>
        </div>

        {notificationSupported && (
          <div className="flex space-x-3">
            {permission !== 'granted' && (
              <Button
                onClick={requestPermission}
                variant="outline"
                className="border-gray-300 text-black hover:bg-gray-50"
              >
                Przyznaj uprawnienia
              </Button>
            )}
            
            {permission === 'granted' && (
              <Button
                onClick={testNotification}
                variant="outline"
                className="border-gray-300 text-black hover:bg-gray-50"
              >
                Testuj powiadomienie
              </Button>
            )}
          </div>
        )}

        {message && (
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
            <p className="text-blue-700 text-sm">{message}</p>
          </div>
        )}

        <div className="bg-gray-50 rounded-lg p-4">
          <h4 className="font-medium text-black mb-2">Jak to dziaÅ‚a?</h4>
          <ul className="text-sm text-gray-600 space-y-1">
            <li>â€¢ WÅ‚Ä…cz przypomnienia i przyznaj uprawnienia przeglÄ…darce</li>
            <li>â€¢ Ustaw interwaÅ‚ miÄ™dzy powiadomieniami (5-120 minut)</li>
            <li>â€¢ Otrzymuj regularne przypomnienia o piciu wody</li>
            <li>â€¢ Powiadomienia dziaÅ‚ajÄ… nawet gdy karta jest nieaktywna</li>
          </ul>
        </div>
      </CardContent>
    </Card>
  )
}
